@import url(partials/reset.css);

@import url(partials/fonts.css);

@import url(partials/swiper.css);

@import url(partials/jquery.fancybox.css);

/* my style */

a {
  text-decoration: none;
}

html {
  height: 100%;
}

body {
  color: #000;
  font-family: "Roboto", sans-serif;
  margin: 0;
  font-size: 1vw;
  background: #ffffff;
  height: 100%;
}

.container {
  margin: 0 auto;
  position: relative;
}

.wrapper {
  overflow: hidden;
}

.footer-block {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  padding: 0.52083vw 1.97917vw 1.97917vw 2.91667vw;
  background: #ffffff;
  width: 100%;
  box-sizing: border-box;
}

.footer-block .woman {
  width: 8.75vw;
  display: block;
}

.footer-block .woman img {
  width: 100%;
  display: block;
}

.footer-block .share {
  display: block;
  width: 1.5625vw;
  position: relative;
}

.footer-block .share svg {
  width: 100%;
  display: block;
}

.footer-block .share:hover svg path {
  fill: #b2d612;
}

.footer-block .share .social-wrapper {
  max-width: 2.34375vw;
  top: 0;
  right: -0.78125vw;
  margin-top: -0.9375vw;
  position: absolute;
  display: block;
  overflow: hidden;
  border-radius: 0.78125vw;
  z-index: 15;
  padding-right: 2.34375vw;
  transition: all 0.3s linear;
}

.footer-block .share .social-block {
  display: flex;
  align-items: center;
  background: #ffffff;
  padding: 0.52083vw;
}

.footer-block .share .social-block a {
  width: 2.39583vw;
  height: 2.39583vw;
  display: block;
  background-size: contain;
  background-position: center center;
  background-repeat: no-repeat;
  cursor: pointer;
}

.footer-block .share .social-block a.fb {
  background-image: url("../img/fb_red.svg");
}

.footer-block .share .social-block a.vk {
  background-image: url("../img/vk-red.svg");
}

.footer-block .share .social-block a.ok {
  background-image: url("../img/ok_red.svg");
}

.footer-block .share .social-block a.tw {
  background-image: url("../img/tw_red.svg");
}

.footer-block .share .social-block a:hover {
  transform: scale(1.1);
}

.footer-block .share .social-block a + a {
  margin-left: 0.52083vw;
}

.footer-block .share:hover .social-wrapper {
  box-shadow: 0 0 1.97917vw rgba(0, 0, 0, 0.26);
  max-width: 15.625vw;
}

